// src/services/TrustScoreService.test.ts

import { describe, it, expect, jest, beforeEach, afterEach } from '@jest/globals';
import { supabase } from '../lib/supabaseClient';
import TrustScoreService from './TrustScoreService';

// --- 1. MOCKING DEPENDÊNCIAS ---

// Mock do Supabase
const mockSupabaseInstance = {
  from: jest.fn(),
};

jest.mock('../lib/supabaseClient', () => ({
  supabase: mockSupabaseInstance,
}));

// Typecast do mock para intellisense
const mockedSupabase = supabase as jest.Mocked<typeof supabase>;

// --- 2. CONFIGURAÇÃO DOS TESTES ---

describe('TrustScoreService', () => {

  // Espiões para métodos privados que queremos verificar se foram chamados
  let spyLogChange: jest.SpiedFunction<any>;
  let spyLogBan: jest.SpiedFunction<any>;
  let spyLogUnban: jest.SpiedFunction<any>;
  let spyBanUser: jest.SpiedFunction<typeof TrustScoreService.banUser>;

  beforeEach(() => {
    jest.clearAllMocks();
    (mockedSupabase.from as jest.Mock).mockReset();

    // Espiamos os métodos de log (privados) e banUser
    spyLogChange = jest.spyOn(TrustScoreService as any, 'logTrustScoreChange')
      .mockImplementation(() => Promise.resolve());
      
    spyLogBan = jest.spyOn(TrustScoreService as any, 'logBanishment')
      .mockImplementation(() => Promise.resolve());
      
    spyLogUnban = jest.spyOn(TrustScoreService as any, 'logUnbanishment')
      .mockImplementation(() => Promise.resolve());
      
    spyBanUser = jest.spyOn(TrustScoreService, 'banUser')
      .mockImplementation(() => Promise.resolve({ success: true }));
  });

  afterEach(() => {
    // Restauramos todos os espiões
    spyLogChange.mockRestore();
    spyLogBan.mockRestore();
    spyLogUnban.mockRestore();
    spyBanUser.mockRestore();
  });

  // --- 3. TESTES DOS MÉTODOS ---

  describe('getTrustScore', () => {
    it('deve retornar o score e o status de banido do usuário', async () => {
      const mockProfile = { trust_score: 3, is_banned: false };

      // Mock do SELECT
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        select: jest.fn(() => ({
          eq: jest.fn(() => ({
            single: jest.fn().mockImplementation(() => Promise.resolve({ data: mockProfile, error: null })),
          })),
        })),
      }));

      const result = await TrustScoreService.getTrustScore('user-1');

      expect(result.success).toBe(true);
      expect(result.data?.trustScore).toBe(3);
      expect(result.data?.isBanned).toBe(false);
      expect(mockedSupabase.from).toHaveBeenCalledWith('profiles');
    });

    it('deve retornar score 5 (padrão) se o usuário não tiver score', async () => {
      const mockProfile = { trust_score: null, is_banned: false }; // Sem score definido

      // Mock do SELECT
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        select: jest.fn(() => ({
          eq: jest.fn(() => ({
            single: jest.fn().mockImplementation(() => Promise.resolve({ data: mockProfile, error: null })),
          })),
        })),
      }));

      const result = await TrustScoreService.getTrustScore('user-1');
      expect(result.success).toBe(true);
      expect(result.data?.trustScore).toBe(5); // Deve assumir o padrão
    });

    it('deve retornar "Usuário não encontrado" em erro PGRST116', async () => {
      // Mock do SELECT (não encontrado)
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        select: jest.fn(() => ({
          eq: jest.fn(() => ({
            single: jest.fn().mockImplementation(() => Promise.resolve({ data: null, error: { code: 'PGRST116' } })),
          })),
        })),
      }));
      
      const result = await TrustScoreService.getTrustScore('user-404');
      expect(result.success).toBe(false);
      expect(result.error).toBe('Usuário não encontrado');
    });
  });

  describe('penalizeNoShow', () => {
    it('deve reduzir o score em 1 e registrar o log', async () => {
      const mockProfile = { trust_score: 4 };

      // 1. Mock do SELECT (para pegar score atual)
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        select: jest.fn(() => ({
          eq: jest.fn(() => ({
            single: jest.fn().mockImplementation(() => Promise.resolve({ data: mockProfile, error: null })),
          })),
        })),
      }));
      
      // 2. Mock do UPDATE (para salvar novo score)
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        update: jest.fn(() => ({
          eq: jest.fn().mockImplementation(() => Promise.resolve({ error: null })),
        })),
      }));
      
      const result = await TrustScoreService.penalizeNoShow('user-penalize');

      expect(result.success).toBe(true);
      expect(result.data?.newScore).toBe(3);
      expect(result.data?.wasBanned).toBe(false);
      
      // Verifica se o UPDATE foi chamado com o novo score
      expect(mockedSupabase.from('profiles').update).toHaveBeenCalledWith(
        expect.objectContaining({ trust_score: 3 })
      );
      
      // Verifica se o Log foi chamado
      expect(spyLogChange).toHaveBeenCalledWith('user-penalize', 4, 3, 'no_show');
      
      // Garante que NÃO baniu
      expect(spyBanUser).not.toHaveBeenCalled();
    });

    it('deve reduzir o score para 0 e chamar banUser', async () => {
      const mockProfile = { trust_score: 1 }; // Último ponto

      // 1. Mock do SELECT
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        select: jest.fn(() => ({
          eq: jest.fn(() => ({
            single: jest.fn().mockImplementation(() => Promise.resolve({ data: mockProfile, error: null })),
          })),
        })),
      }));
      
      // 2. Mock do UPDATE
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        update: jest.fn(() => ({
          eq: jest.fn().mockImplementation(() => Promise.resolve({ error: null })),
        })),
      }));
      
      const result = await TrustScoreService.penalizeNoShow('user-banir');

      expect(result.success).toBe(true);
      expect(result.data?.newScore).toBe(0);
      expect(result.data?.wasBanned).toBe(true);
      
      // Verifica se o UPDATE foi chamado com 0
      expect(mockedSupabase.from('profiles').update).toHaveBeenCalledWith(
        expect.objectContaining({ trust_score: 0 })
      );
      
      // Verifica se o Log foi chamado
      expect(spyLogChange).toHaveBeenCalledWith('user-banir', 1, 0, 'no_show');
      
      // VERIFICA SE O BANIMENTO FOI CHAMADO
      expect(spyBanUser).toHaveBeenCalledWith('user-banir', 'Trust score atingiu 0');
    });
  });

  describe('banUser', () => {
    it('deve banir o usuário e registrar o log', async () => {
      // Libera o spy para testar a implementação real
      spyBanUser.mockRestore(); 
      
      // 1. Mock do UPDATE (banimento)
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        update: jest.fn(() => ({
          eq: jest.fn().mockImplementation(() => Promise.resolve({ error: null })),
        })),
      }));
      
      const result = await TrustScoreService.banUser('user-banir', 'Motivo teste');

      expect(result.success).toBe(true);
      
      // Verifica se o UPDATE foi chamado corretamente
      expect(mockedSupabase.from('profiles').update).toHaveBeenCalledWith(
        expect.objectContaining({ is_banned: true, trust_score: 0 })
      );
      
      // Verifica se o log de banimento foi chamado
      expect(spyLogBan).toHaveBeenCalledWith('user-banir', 'Motivo teste');
    });
  });

  describe('unbanUserAfterPayment', () => {
    it('deve desbanir, resetar score, registrar pagamento e log', async () => {
      // 1. Mock do UPDATE (unban)
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        update: jest.fn(() => ({
          eq: jest.fn().mockImplementation(() => Promise.resolve({ error: null })),
        })),
      }));
      
      // 2. Mock do INSERT (payment)
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        insert: jest.fn().mockImplementation(() => Promise.resolve({ error: null })),
      }));
      
      const result = await TrustScoreService.unbanUserAfterPayment('user-unban', 'pay-123');
      
      expect(result.success).toBe(true);
      expect(result.data?.newScore).toBe(5);
      
      // Verifica se o UPDATE (unban) foi chamado
      expect(mockedSupabase.from('profiles').update).toHaveBeenCalledWith(
        expect.objectContaining({ is_banned: false, trust_score: 5 })
      );
      
      // Verifica se o INSERT (payment) foi chamado
      expect(mockedSupabase.from('payments').insert).toHaveBeenCalledWith(
        expect.objectContaining({ user_id: 'user-unban', stripe_payment_id: 'pay-123' })
      );
      
      // Verifica se o log de unban foi chamado
      expect(spyLogUnban).toHaveBeenCalledWith('user-unban', 'pay-123');
    });
  });

  describe('penalizeNoShowsForEvent', () => {
    
    // Espião específico para penalizeNoShow
    let spyPenalize: jest.SpiedFunction<typeof TrustScoreService.penalizeNoShow>;
    
    beforeEach(() => {
      spyPenalize = jest.spyOn(TrustScoreService, 'penalizeNoShow')
        .mockImplementation(() => Promise.resolve({ success: true }));
    });
    
    afterEach(() => {
      spyPenalize.mockRestore();
    });

    it('deve chamar penalizeNoShow para cada usuário que não compareceu', async () => {
      const mockNoShows = [
        { id: 'part-1', user_id: 'user-A' },
        { id: 'part-1', user_id: 'user-B' },
      ];

      // 1. Mock do SELECT (no-shows)
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        select: jest.fn(() => ({
          eq: jest.fn(() => ({
            eq: jest.fn(() => ({
              eq: jest.fn(() => ({
                eq: jest.fn().mockImplementation(() => Promise.resolve({ data: mockNoShows, error: null })),
              })),
            })),
          })),
        })),
      }));
      
      const result = await TrustScoreService.penalizeNoShowsForEvent(1);
      
      expect(result.success).toBe(true);
      expect(result.data?.penalized).toBe(2);
      
      // Verifica se o método de penalizar foi chamado 2x
      expect(spyPenalize).toHaveBeenCalledTimes(2);
      expect(spyPenalize).toHaveBeenCalledWith('user-A');
      expect(spyPenalize).toHaveBeenCalledWith('user-B');
    });

    it('não deve fazer nada se não houver no-shows', async () => {
      // 1. Mock do SELECT (vazio)
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        select: jest.fn(() => ({
          eq: jest.fn(() => ({
            eq: jest.fn(() => ({
              eq: jest.fn(() => ({
                eq: jest.fn().mockImplementation(() => Promise.resolve({ data: [], error: null })),
              })),
            })),
          })),
        })),
      }));
      
      const result = await TrustScoreService.penalizeNoShowsForEvent(1);
      
      expect(result.success).toBe(true);
      expect(result.data?.penalized).toBe(0);
      
      // Garante que não penalizou ninguém
      expect(spyPenalize).not.toHaveBeenCalled();
    });
  });

});
